@media only screen and (min-width: 501px) {
	#hdr {
		position: sticky;
		top: 0;
		width: 100%;
		height: 3.4rem;
		padding: 0.1rem;
		box-sizing: content-box;
		color: white;
		background-color: black;
		z-index: 3;
	}

	/*Header elements*/
	.hdr-block {
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		position: relative;
	}

	.logo {
		font-family: "Modak";
		font-size: 2rem;
		margin-right: 10%;
	}

	.logo:hover {
		color: var(--sel-color);
	}

	.hdr-btns {
		height: 100%;
		display: inline-grid;
		grid-template-columns: repeat(3, 1fr);
		align-items: center;
		padding: 0 1vw;
		column-gap: 2vw;
	}

	.hdr-btn {
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;
		color: white;
		position: relative;
		box-shadow: grey 0px 4px 6px -2px inset;
		padding: 0 0.5vw;
		font-size: 1rem;
		padding: 0 0.2vw;
	}

	.hdr-btn:hover {
		box-shadow: grey 0px 4px 6px -4px inset;
		outline: 1px dotted grey;
		transition: 0.4s;
	}

	.hdr-btn:hover span {
		color: var(--sel-color);
		cursor: pointer;
	}

	.hdr-btn svg {
		stroke: white;
		fill: white;
		width: 2rem;
		height: 2rem;
	}

	.hdr-btn:hover svg:not(.hdr-menu svg) {
		stroke: var(--sel-color);
		fill: var(--sel-color);
	}

	/* Header drop-down menu */
	.hdr-menu {
		top: 100%;
		right: 0;
		position: absolute;
		background-color: black;

	}

	.hdr-menu li {
		border-bottom: 0.1vw dotted white;
		min-width: 15rem;
	}

	.hdr-menu li:last-child {
		border: none;
	}

	.hdr-menu li:hover {
		background-color: rgba(225, 225, 225, 0.3);
	}

	.hdr-menu li svg {
		fill: white;
		stroke: white;
		width: 1.6rem;
		height: 1.6rem;
	}

	.hdr-menu li a,
	.hdr-menu li button {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: white;
		width: 100%;
		height: 100%;
		column-gap: 1rem;
		padding: 0.8rem;
		font-size: 1.1rem;
	}

	/*bookmarks and catalog drop-down menu*/
	.hdr-bookmarks,
	.hdr-catalog {
		position: relative;
	}

	/* Favorite icon style */
	.favorite-icon {
		fill: none !important;
	}

	.favorite-icon.favor-icon-sel {
		fill: rgba(225, 225, 225, 0.4) !important;
	}
}

@media only screen and (max-width: 500px) {
	#hdr {
		position: sticky;
		top: 0;
		width: 100%;
		box-sizing: content-box;
		color: white;
		background-color: black;
		z-index: 3;
		padding: 1vw 0;
	}

	/*Header elements*/
	.hdr-block {
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		position: relative;
	}

	.logo {
		font-family: "Modak";
		font-size: 7.2vw;
		margin-right: 2%;
	}

	.logo:hover {
		color: var(--sel-color);
	}

	.hdr-btns {
		height: 100%;
		display: inline-grid;
		grid-template-columns: repeat(3, 1fr);
		align-items: center;
		column-gap: 2vw;
	}

	.hdr-btn {
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;
		color: white;
		position: relative;
		box-shadow: grey 0px 4px 6px -2px inset;
		padding: 0 0.5vw;
		font-size: 3.8vw;
		padding: 0 0.2vw;
		position: relative;
	}

	.hdr-btn:hover {
		box-shadow: grey 0px 4px 6px -4px inset;
		outline: 1px dotted grey;
		transition: 0.4s;
	}

	.hdr-btn svg {
		stroke: white;
		fill: white;
		width: 8vw;
		height: 8vw;
	}

	/* Header drop-down menu */
	.hdr-menu {
		position: fixed;
		top:0;
		left:0;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		background-color: rgba(0,0,0,0.3);
		z-index: 4;
	}

	.hdr-menu ul {
		width: 96%;
		background-color: black;
	}

	.hdr-menu li {
		border-bottom: 0.1vw dotted white;
		min-width: 70vw;
	}

	.hdr-menu li:last-child {
		border: none;
	}

	.hdr-menu li svg {
		fill: white;
		stroke: white;
		width: 8vw;
		height: 8vw;
	}

	.hdr-menu li a,
	.hdr-menu li button {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: white;
		width: 100%;
		height: 100%;
		column-gap: 1vw;
		padding: 4vw 2vw;
		font-size: 5vw;
	}

	/* Favorite icon style */
	.favorite-icon {
		fill: none !important;
	}

	.favorite-icon.favor-icon-sel {
		fill: rgba(225, 225, 225, 0.4) !important;
	}
}


/*Header search field*/
.hdr-srch {
	position: absolute;
	height: 100%;
	width: 100%;
	display: none;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.8);
	z-index: 20;
	color: black;
}

.hdr-srch-shown {
	display: flex;
}

.hdr-srch form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: relative;
	width: 50%;
	height: 80%;
}

.hdr-srch input {
	width: 100%;
	height: 100%;
	border-radius: 1rem;
	padding: 0 1rem;
	font-size: 1.2rem;
	outline: none;
}

.hdr-srch ul {
	position: absolute;
	flex-direction: column;
	row-gap: 1rem;
	font-size: 1rem;
	top: 100%;
	width: 100%;
	color: black;
	border: 1px solid grey;
	border-top: none;
	background-color: white;
	border-bottom-left-radius: 1rem;
	border-bottom-right-radius: 1rem;
	padding: 1rem;
}

.hdr-srch .srch-hint-shown input {
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
}

/*Подсказка поиска*/
.srch-hint {
	position: absolute;
	top: 100%;
	right: 0;
	left: 0;
	box-sizing: content-box;
	overflow: hidden;
	background-color: white;
	border: 1px solid grey;
	border-top: none;
	border-top-left-radius: 0;
	border-top-right-radius: 0;
	z-index: 3;
	display: none;
}

.srch-hint-item {
	cursor: pointer;
}

.srch-hint-item:hover {
	background-color: rgba(225, 225, 225, 0.5);
}

.srch-hint-item em {
	color: grey;
}

.srch-hint-item b {
	background-color: rgba(225, 225, 225, 0.5);
}

.srch-hint-item-active {
	background-color: rgba(225, 225, 225, 0.5);
}


/* Уведомления около икононок */
.icon-notification {
	position: absolute;
	top: 1%;
	right: 1%;
	width: 1rem;
	height: 1rem;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	border-radius: 1rem;
	font-size: 0.6rem;
	font-weight: bold;
	z-index: 2;
	background-color: var(--sel-color);
	color: black;
	border: 1px solid white;
}